.footer{

    width : 100vw;

    .attribution{
        position: absolute;
        left: 50%;
        transform: translate(-50%);
        bottom: 1rem;
    }


    button{
        position: absolute;
        bottom: 1rem;
        padding: 7px 25px;
        right: 1rem;
        border-radius: .4rem;
        border: 3px solid $header-outline;
        color: lighten($header-outline,20%);
        background: none;
        font-weight: 600;
        letter-spacing: 2px;


        &:hover{
            background-color: rgba($color: #ffffff, $alpha: 1.0);
            color: $Dark-Text;
        }



    }

}
